  }
.container {
  flex: 80% 1;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  height: 100vh;
  overflow-y: auto;
  /* We're calling this animation "crawl" */ }
  .container section {
    flex: 1 0 100vh;
    display: flex;
    flex-direction: column; }
  .container .starwars {
    background-color: black;
    overflow: hidden;
    -webkit-perspective: 300px;
            perspective: 300px;
    justify-content: center;
    color: #feda4a;
    font-family: 'Pathway Gothic One', sans-serif;
    font-size: 500%;
    font-weight: 600;
    letter-spacing: 6px;
    line-height: 150%;
    text-align: justify; }
  .container .crawl {
    position: relative;
    -webkit-transform-origin: 50% 70%;
            transform-origin: 50% 70%;
    align-items: center;
    justify-content: center;
    -webkit-animation: crawl 60s linear;
            animation: crawl 60s linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; }
    .container .crawl img {
      width: 100vw; }
  .container .fade {
    position: absolute;
    width: 100%;
    min-height: 60vh;
    top: 0;
    background-image: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(75%, black));
    background-image: linear-gradient(0deg, transparent, black 75%);
    z-index: 100; }
  .container .map-container {
    width: 100%;
    min-height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .container .map-text {
    overflow-y: scroll;
    text-align: center;
    background: #a1a1a1;
    width: 95%;
    height: 48%; }
    .container .map-text .shadow {
      color: white;
      font-family: 'Black Han Sans', serif;
      -webkit-transition: all 0.12s ease-out;
      -moz-transition: all 0.12s ease-out;
      -ms-transition: all 0.12s ease-out;
      -o-transition: all 0.12s ease-out; }
      .container .map-text .shadow:hover {
        position: relative;
        top: -3px;
        left: -3px; }
    .container .map-text h1 {
      font-size: 4em;
      text-shadow: 1px 1px #fe4902,  2px 2px #fe4902,  3px 3px #fe4902; }
      .container .map-text h1:hover {
        text-shadow: 1px 1px #fe4902,  2px 2px #fe4902,  3px 3px #fe4902,  4px 4px #fe4902,  5px 5px #fe4902,  6px 6px #fe4902; }
    .container .map-text p {
      font-size: 2em;
      text-shadow: 1px 1px #4902fe,  2px 2px #4902fe,  3px 3px #4902fe; }
      .container .map-text p:hover {
        text-shadow: 1px 1px #4902fe,  2px 2px #4902fe,  3px 3px #4902fe,  4px 4px #4902fe,  5px 5px #4902fe,  6px 6px #4902fe; }
  .container .map-content {
    width: 95%;
    height: 48%; }
  @media (orientation: landscape) {
    .container .map-container {
      flex-direction: row; }
    .container .map-content {
      width: 48%;
      height: 98%; }
    .container .map-text {
      width: 48%;
      height: 98%; } }

@-webkit-keyframes crawl {
  0% {
    /* The element starts below the screen */
    top: 1300px;
    /* Rotate the text 20 degrees but keep it close to the viewer */
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -3000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

@keyframes crawl {
  0% {
    /* The element starts below the screen */
    top: 1300px;
    /* Rotate the text 20 degrees but keep it close to the viewer */
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -3000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

